@import url(./utils/vendor/normalize.css);
@import url('./utils/vendor/fonts/fonts.css');
body {
    margin: 0;
    font-family:
        'Roboto',
        -apple-system,
        BlinkMacSystemFont,
        'Segoe UI',
        'Roboto',
        'Oxygen',
        'Ubuntu',
        'Cantarell',
        'Fira Sans',
        'Droid Sans',
        'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: 'Roboto', source-code-pro, Menlo, Monaco, Consolas,
        'Courier New', monospace;
}
